import './Menu.css'

import React, { useContext } from 'react'
import {Link} from 'react-router-dom'

import {AppContext} from '../data/Store'

function toggleMenu() {
    const menuList = document.getElementById('menu-list')
    menuList.classList.toggle('active')
    const iconMenu = document.getElementById('hamburger')
    if (menuList.classList.contains('active')) {
        iconMenu.innerHTML = 'close'
    } else {
        iconMenu.innerHTML = 'menu'
    }
}

function toogleActiveSubmenu(e) {
    const children = e.target.children
    console.log('CHILDREN => ', children)
}

const Menu = props => {
    const {user} = useContext(AppContext)

    return (
        <nav className='Menu'>
            
            <ul id='menu-list'>
                <li className={`menu-item ${user.isAdministrator ? 'active' : ''}`} onClick={e => toogleActiveSubmenu(e)}><a>Administração</a>
                    <ul id='submenu-adm' className='submenu'>
                        <li className='submenu-item'><a href="/categories" >Categorias</a></li>
                        <li className='submenu-item'><a href="/">Denúncias</a></li>
                        <li className='submenu-item'><a href="/">Usuários Bloqueados</a></li>
                    </ul>
                </li>
                <li className={`menu-item ${user.validToken ? 'active' : ''}`}>
                    <a href="profile">Perfil</a>
                </li>
                <li  className={`menu-item ${user.validToken ? 'active' : ''}`}><a>Blog</a>
                    <ul id='submenu-blog' className='submenu'>
                        <li className='submenu-item'><a href="blogpreferences">Preferências</a></li>
                        <li className='submenu-item'><a href="in">Minhas Conexões</a></li>
                        <li className='submenu-item'><a href="/">Meus Indicadores</a></li>
                        <li className='submenu-item'><a href='/myarticles'>Meus Artigos</a></li>
                    </ul>
                </li>
                <li className='menu-item active'><a href="/">Pesquisar</a></li>
                <li className='menu-item active'><a href='/article'>Escrever</a></li>
                <li className={`menu-item ${user.validToken ? 'active' : ''}`}><a href="/">Notificações</a></li>
                <li className={`menu-item ${user.validToken ? 'active' : ''}`}><a href="logout">Sair</a></li>
            </ul>

            <button id='btn-mobile' onClick={() => toggleMenu()}>
                <span id='hamburger' className="material-icons-outlined">menu</span>
            </button>
            {/* <span id='iconCloseMenu' class="material-icons">&#xe5cd;</span> */}
        </nav>
    )
}

export default Menu